.wp-block-create-block-header-section {
	background-color: #21759b;
	color: #fff;
	padding: 2px;
}
.headerImage{
	background-position: center center;
	background-repeat: no-repeat;
	height: 100vh;
	width: 100%;
	position: initial;
	background-size: cover;
}
.wp-block-post-content {
    max-width: 1450px !important;
    margin-left: auto !important;
    margin-right: auto !important;
}
.headerImage > header{
	text-align: center;
	padding-top: 10px;
}
.headerImage > header button{
	padding: 10px 15px;
	margin-right: 15px;
	background-color: transparent;
	color: #fff;
	font-size: 18px;
	cursor: pointer;
}
.heading{
	margin-top: 10%;
}
.hexdingtxt{
	font-size: 3.5rem;
	color: #fff;
	font-weight: 700;
	line-height: 40px !important;
}
.headerText {
	max-width: 500px;
	margin: 0 auto;
	text-align: left;
    color:#fff;
	display: block;
	

}
.headerText p{
	color:#fff;
}